import navbar from '@/components/navbar/navbar.vue'
export default {
	components: {
		navbar
	},

	data() {
		return {
			title: 'input',
			focus: false,
			//登录参数
			loginParams: {
				accountName: '',
				password: ''
			},
			isShowEye: false, //是否显示密码
		}
	},
	methods: {

		//验证方法
		checkDatalogin() {
			if (!this.loginParams.accountName) {
				uni.showToast({
					title: '账户名不能为空',
					icon: 'none',
					mask: true,
					duration: 1000,
				});
				return false
			};
			if (!this.loginParams.password) {
				uni.showToast({
					title: '密码不能为空',
					icon: 'none',
					mask: true,
					duration: 1000,
				});
				return false
			};
			return true
		},
		//登录方法
		login( ) {
			if(this.checkDatalogin() ){
				let _param = {
					accountName: this.loginParams.accountName,
					password: this.loginParams.password,
				};
				
				this.$util.httpPost("/accountCenter/account/login/" + "zs", _param).then(res => {
					if (Number(res.data.statusCode) === 200) {
						uni.showToast({
							title: '登录成功',
							icon: 'success',
							mask: true,
							duration: 1000,
						});
						//把用户信息进行存储
						uni.setStorage({
						    key: 'useinfo',
						    data: res.data.data,
						    success: function () {
						        console.log('success');
						    }
						});
					
						// 把登录状态存储到 本地
						// uni.setStorage({
						//     key: 'isLogin',
						//     data: true,
						//     success: function () {
						//         // console.log('success');
						//     }
						// });
						//登陆成功后2s后跳转首页
						setTimeout(() => {
						  uni.redirectTo({
						  	url: "../index/index"
						  })
						 },2000)   
						
					} else {
						uni.showToast({
							title: res.data.statusMsg,
							icon: 'none',
							mask: true,
							duration: 1000,
						})
					}
				});
				
			}

		},
		//忘记密码
		forgetPwd() {
			uni.navigateTo({
				url: "../changepwd/changepwd"
			})
		},

		/**
		 * @desc 密码显隐操作
		 */
		passwordClick() {
			this.isShowEye = !this.isShowEye;
		},

	}


}
